import { Component, OnInit } from '@angular/core';
import { ResponseFile } from './interface/response-data';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {

  public uploadFiles: ResponseFile[];
  public move = 0;

  constructor() {}

  ngOnInit(): void {}

  changeImage(isNext: boolean): void {
    const amoutFiles = this.uploadFiles.length - 1;
    if (this.move === 0 && !isNext) {
      this.move = amoutFiles;
    } else if (this.move === amoutFiles && isNext) {
      this.move = 0;
    } else {
      this.move += isNext ? 1 : -1;
    }
  }
}
