import { Component, OnInit, Output, EventEmitter, HostListener, ViewChild, ElementRef, Input } from '@angular/core';
import { ResponseFile, SearchObject } from 'src/app/interface/response-data';

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.scss']
})
export class SidebarComponent implements OnInit {

  private files: ResponseFile[] = [];
  private searchObjects: SearchObject[] = [
    { id: 1, name: 'Продавец' },
    { id: 2, name: 'Витрина выпечка' },
    { id: 3, name: 'Витрина хлеб' },
  ];

  @Output() сhangesFiles = new EventEmitter<Array<ResponseFile>>();
  @Output() changeImage = new EventEmitter<boolean>();
  @ViewChild('input1', {static: false}) input1: ElementRef;
  @ViewChild('input2', {static: false}) input2: ElementRef;

  @HostListener('window:keydown', ['$event']) handleKeyboardEvent(event: KeyboardEvent): void {
    if (event.key === 'ArrowRight') {
      this.nextImg(true);
    }

    if (event.key === 'ArrowLeft') {
      this.nextImg(false);
    }
  }

  constructor() {}

  ngOnInit(): void {}

  send(): void {

  }

  changesImages(event: any): void {
    const files = Array.from(event.srcElement.files);
    this.files = files.map((file: File, i: number): ResponseFile => {
      return { id: i, name: `${ i }`, file, objects: this.searchObjects };
    });
    this.сhangesFiles.emit(this.files);
  }

  getImagesObject(): void {
    // тут http запрос к апи(пока хз куда)
    // this.сhangesFiles.emit(this.files);
  }

  nextImg(isNext: boolean): void {
    if (this.files && this.files.length > 1) {
      this.changeImage.emit(isNext);
    }
  }

  clean(): void {
    this.input1.nativeElement.value = '';
    this.input2.nativeElement.value = '';
  }
}
